body {
  box-sizing: border-box;
  width: 200px;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding-block: 32px 16px;
  padding-inline: 16px;
}

.card__image {
  justify-content: center;
  width: 100%;
  display: flex;
}

.card__image-img {
  background-image: url("imac.202c52ff.jpeg");
  background-size: 100% 100%;
  width: 160px;
  height: 134px;
}

.card__description {
  text-align: left;
  color: #060b35;
  margin-block: 40px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  text-align: left;
  margin-block: 4px 16px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 8px;
  display: flex;
}

.card__price-name {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-weight: 800;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.stars {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__reviews {
  color: #060b35;
}
